<template>
  <div class="upfile">
    <el-form>
      <el-form-item label="">
        <el-upload
          class="upload-demo"
          action="#"
          accept="video/*"
          :limit="1"
          :file-list="fileList"
          :before-upload="beforeUpload">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传mp4文件，且不超过100M</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="视频标题">
        <el-input v-model="videoname"></el-input>
      </el-form-item>
      <el-form-item label="视频简介">
        <el-input v-model="text"></el-input>
      </el-form-item>
      <el-form-item label="视频类型">
        <el-radio-group v-model="radio">
          <el-radio :label="1">动漫</el-radio>
          <el-radio :label="2">电影</el-radio>
          <el-radio :label="3">电视剧</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="submitUpload()">上传</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
const axios = require('axios')
export default {
  name: 'upfile',
  inject: ['user'],
  data () {
    return {
      fileList: [],
      fileName: '',
      files: '',
      radio: 1,
      videoname: '',
      text: ''
    }
  },
  methods: {
    beforeUpload (file) {
      //判断文件大小是否合格
      let isLtSize = file.size / 1024 / 1024 < 100
      if (!isLtSize) {
        this.$message.error('上传图片大小不能超过 100MB!');
        return false
      }
      this.fileList.unshift(file)
      this.files = file
      this.fileName = file.name
      this.fileName = file.name
      return false // 返回false不会自动上传
    },

    submitUpload () {
      if (this.fileName == '') {
        this.$message.warning('请选择要上传的文件！')
        return
      }
      if (this.videoname != '') {
        this.fileName = this.videoname
      }
      let fileFormData = new FormData()
      let radio = this.radio, text = this.text
      //filename是键，file是值，就是要传的文件，test.zip是要传的文件名
      fileFormData.append('file', this.files, this.fileName)
      axios({
        method: 'post',
        url: '/upfile?uid='+this.user.uid+'&text='+text+'&radio='+radio,
        data: fileFormData,
        header: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(resp => {
        if(resp.data.result === true){
          this.$alert('上传成功', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          this.$alert('上传失败', '提示', {
            confirmButtonText: '确定'
          })
        }
      }).catch(failResponse => {})
    }
  }
}
</script>

<style scoped>
  .upfile{
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }


</style>



