<template>
  <div class="content">
    <el-menu
      class="categories"
      :default-active="menuIndex"
      @select="handleSelect"
      active-text-color="red">
      <el-menu-item index="1"  >
        <i class="el-icon-menu"></i>
        <span slot="title">收到的评论</span>
      </el-menu-item>
      <el-menu-item index="2"  >
        <i class="el-icon-menu"></i>
        <span slot="title">修改密码</span>
      </el-menu-item>
    </el-menu>

    <div class="rate-box" v-show="conShow">
      <div class="rate-item" v-for="item in rateList" v-bind:key="item.id" >
        <div class="top">
          <img src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
               class="image">{{item.id}} 用户在 <span style="margin-left: 20px;color: #999">{{item.date}}</span>
                时对您的某个视频发出了评论：
        </div>
        <div style="padding-right: 15px">
          {{item.content}}
        </div>
      </div>
    </div>

    <el-form :model="passForm" :rules="rules" class="change-container" label-position="left"
             label-width="0px" v-loading="loading" v-show="chShow">
      <h3 class="change_title">修改密码</h3>
      <el-form-item prop="oldpass">
        <el-input type="password" v-model="passForm.oldpass"
                  auto-complete="off" placeholder="旧密码"></el-input>
      </el-form-item>
      <el-form-item prop="newpass">
        <el-input type="password" v-model="passForm.newpass"
                  auto-complete="off" placeholder="新密码"></el-input>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 40%;background: #505458;border: none" v-on:click="change">更改</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  const axios = require('axios')
  export default {
    name: 'charge',
    inject: ['user'],
    data () {
      return {
        rules: {
          oldpass: [{required: true, message: '密码不能为空', trigger: 'blur'}],
          newpass: [{required: true, message: '密码不能为空', trigger: 'blur'}]
        },
        checked: true,
        passForm: {
          oldpass: '',
          newpass: ''
        },
        loading: false,
        menuIndex: '1',
        conShow: true,
        chShow: false,
        rateList: []
      }
    },

    //连接服务器获取评论
    created () {
      axios({
        method: 'post',
        url: '/index?action=getAllComment',
        data: {uid: this.user.uid},
        header: {
          // 如果写成contentType会报错
          'Content-Type': 'application/json'
        }
      }).then(resp => {
        //服务端返回一个评论数组
        if (!resp.data.comment) {
          this.$alert('获取信息失败！', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          for(let item of resp.data.comment) {
            this.rateList.unshift({
              id: item.uid,
              content: item.text,
              date: item.time
            });
          }
        }
      }).catch(failResponse => {
      })
    },

    methods: {

      handleSelect (index) {
        if (index == 2) {
          this.conShow = false
          this.chShow = true
        }
        else {
          this.conShow = true
          this.chShow = false
        }
      },

      check (pass) {
        let regpass = /^\w{6,18}/
        if (!regpass.test(pass)) {
          this.$alert('密码格式错误', '提示', {
            confirmButtonText: '确定'
          })
          return false
        }
        return true
      },

      change () {
        if (!this.check(this.passForm.newpass)){
          return
        }
        axios({
          method: 'post',
          url: '/index?action=change',
          data: {'uid': this.user.uid,'oldpass': this.passForm.oldpass, 'newpass': this.passForm.newpass},
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
          if (resp.data.result === false) {
            this.$alert('更改失败！请检查是否输入错误！', '提示', {
              confirmButtonText: '确定'
            })
          } else {
            this.$alert('更改成功', '提示', {
              confirmButtonText: '确定'
            })
          }
        }).catch(failResponse => {})
      }
    }
  }
</script>

<style scoped>
  .content{
    width: 1200px;
    margin: 0 auto;
    padding-bottom: 50px;
    height: 600px;
  }
  .categories {
    position: fixed;
    top: 123px;
    width: 150px;
  }
  .change-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .change_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .image{
    width: 30px;
    height: 30px;
    border-radius: 50px;
  }
  .rate-box{
    text-align: left;
    padding: 0 10px;
    width: 550px;
    position: relative;
    left: 250px;
    top: 100px;
    background: #fff;
    box-shadow: 0 0 15px #cac6c6;
  }
  .rate-item{
    border-bottom: 1px solid #cfcfcf;
    padding-bottom: 5px;
    position: relative;
  }
  .rate-item .top{
    padding: 15px 0;
  }
  .rate-item .top img{
    vertical-align: middle;
    margin-bottom: 3px;
    margin-right: 10px;
  }
</style>



